import { userInfo } from './api';

const fetchUserInfo = (context) => {
	return new Promise((resolve, reject) => {
    	userInfo()
	    	.then((response) => {
		  		context.commit('USER_INFO', response.data.data);
		  		resolve(response.data.data);
			})
			.catch((error) => {
		  		console.error(error);
		  		reject(error);
			});
    });
};

export default {
  fetchUserInfo,
};