		font-family: "Roboto";
		margin-right: 5px;
	}
	.m-portlet.m-portlet-bet-report .m-portlet__head{
		height: auto;
		border-bottom: 1px solid #ebedf2;
	}
	/*.m-portlet.m-portlet-bet-report .m-portlet__body {
	    padding: 2.2rem 0 0 0;
	}*/
	.m-portlet-bet-report .filter{
		margin: 15px 0 0 0px;
