	}
	.m-portlet-bet-report .table tfoot td{
    	font-weight: 600;
	}
	/*.m-portlet-bet-report table tfoot td:last-child,*/
	/*.m-portlet-bet-report table thead th:last-child{
		border-bottom:  0px solid #ffffff
	}*/
	.m-portlet-bet-report .table tfoot td:first-child,
	.m-portlet-bet-report .table thead th:first-child{
		border-left:  0px solid #ffffff
	}
	.m-portlet-bet-report .table tbody tr.even{
		background-color: #f9f9f9;
