	    padding: .3rem;
	}
	.m-portlet-bet-report table.dataTable thead .sorting:before, 
	.m-portlet-bet-report table.dataTable thead .sorting:after, 
	.m-portlet-bet-report table.dataTable thead .sorting_asc:before, 
	.m-portlet-bet-report table.dataTable thead .sorting_asc:after, 
	.m-portlet-bet-report table.dataTable thead .sorting_desc:before, 
	.m-portlet-bet-report table.dataTable thead .sorting_desc:after, 
	.m-portlet-bet-report table.dataTable thead .sorting_asc_disabled:before, 
	.m-portlet-bet-report table.dataTable thead .sorting_asc_disabled:after, 
	.m-portlet-bet-report table.dataTable thead .sorting_desc_disabled:before, 
	.m-portlet-bet-report table.dataTable thead .sorting_desc_disabled:after{
		bottom: 0.4em;
	}
	@media (min-width: 1700px){
		.m-portlet-bet-report .col-xxl-6 {
			max-width: 50%;
			flex: 0 0 50%;
		}
		.m-portlet-bet-report .col-xxl-3{
			max-width: 25%;
			flex: 0 0 25%;
		}
	}
	.remove{
		text-decoration: line-through;
	}
</style>