						<input type="text" class="form-control m-input" v-model="propFilter['2d']">
					</div>
				</div>
				<div class="col-12 col-lg-3">
					<div class="input-group">
						<div class="input-group-prepend">
							<div class="input-group-text limit-point">
								<label class="m-checkbox m-checkbox--success">
									<input v-model="filterVisible['3d']" type="checkbox">
									<span></span>
								</label>
							</div>
							<span class="input-group-text">3D &gt;=</span>
						</div>
						<input type="text" class="form-control m-input" v-model="propFilter['3d']">
					</div>
				</div>
				<div :class="'col-12 ' + (gameFilter == 2 ? 'col-lg-2' : 'col-lg-3')">
					<div class="input-group">
						<div class="input-group-prepend">
							<div class="input-group-text limit-point">
								<label class="m-checkbox m-checkbox--success">
									<input v-model="filterVisible['4d']" type="checkbox">
									<span></span>
								</label>
							</div>
							<span class="input-group-text">4D &gt;=</span>
						</div>
						<input type="text" class="form-control m-input" v-model="propFilter['4d']">
					</div>
				</div>
				<div :class="'col-12 ' + (gameFilter == 2 ? 'col-lg-2' : 'col-lg-3')">
					<div class="input-group">
						<div class="input-group-prepend">
							<div class="input-group-text limit-point">
								<label class="m-checkbox m-checkbox--success">
									<input v-model="filterVisible['da']" type="checkbox">
									<span></span>
								</label>
							</div>
							<span class="input-group-text">Đá thẳng &gt;=</span>
						</div>
