				<div v-if="gameFilter == 2" :class="'col-12 ' + (gameFilter == 2 ? 'col-lg-2' : 'col-lg-3')">
					<div class="input-group">
						<div class="input-group-prepend">
							<div class="input-group-text limit-point">
								<label class="m-checkbox m-checkbox--success">
									<input v-model="filterVisible['dx']" type="checkbox">
									<span></span>
