					</div>
				</div>
			</div>
			<div class="row" style="padding: 15px;">
				<div class="col-12 col-sm-5">
					<button @click="this.getBettedRatioData" type="button" class="btn btn-brand m-btn m-btn--custom">Xem</button>
				</div>
			</div>
			
		</div>
		<div class="m-portlet__body">
			<div class="m-section">
				<div class="m-section__content">
					<div class="row table-content">
						<div v-if="filterVisible['2d']" class="col-12 col-md-6">
							<betted-table
								type="2d"
								title="2D"
								:header="header[gameFilter]['2d']"
								:channels="channel"
								:data="data"
								:game="gameFilter"
							></betted-table>
						</div>
						<div v-if="filterVisible['3d']" class="col-12 col-md-6">
							<betted-table
								type="3d"
								title="3D"
								:header="header[gameFilter]['3d']"
								:channels="channel"
								:data="data"
								:game="gameFilter"
							></betted-table>
						</div>
						<div v-if="filterVisible['4d']" class="col-12 col-md-4">
							<betted-table
								type="4d"
								title="4D"
								:header="header[gameFilter]['4d']"
								:channels="channel"
								:data="data"
								:game="gameFilter"
							></betted-table>
						</div>
						<div v-if="filterVisible['da']" class="col-12 col-md-4">
							<betted-table
								type="da"
								title="Đá"
								:header="header[gameFilter]['da']"
								:channels="channel"
								:data="data"
								:game="gameFilter"
							></betted-table>
						</div>
						<div v-if="gameFilter == 2 && filterVisible['dx']" class="col-12 col-md-4">
							<betted-table
								type="dx"
								title="Đá xiên"
								:header="header[gameFilter]['dx']"
								:channels="channel"
