</template>

<script>
	import moment from 'moment';
	import { mapGetters } from 'vuex';
	import BettedRatioTable from "../BettedRatioTable";
	import groupBy from 'lodash/groupBy';

	export default {
		data(){
			let vm = this;

	        return {
	        	gameFilter: 2,
	        	header: {
					1: {
						"2d": [{'id':[1], 'value': 'Đầu', 'key': 'dau'},{'id':[2], 'value': 'Đuôi', 'key': 'duoi'},{'id':[3,4,5], 'value': 'Blô', 'key': 'blo'}],
						"3d": [{'id':[6], 'value': 'Đầu', 'key': 'dau'},{'id':[7], 'value': 'Đuôi', 'key': 'duoi'},{'id':[8,9,10], 'value': 'Blô', 'key': 'blo'}],
						"4d": [{'id':[11], 'value': 'Đuôi', 'key': 'duoi'},{'id':[12,13], 'value': 'Blô', 'key': 'blo'}],
						"da": [{'id':[14,15,16], 'value': 'Đá thẳng', 'key': 'da'}]
					},
					2: {
						"2d": [{'id':[1], 'value': 'Đầu', 'key': 'dau'},{'id':[2], 'value': 'Đuôi', 'key': 'duoi'},{'id':[3], 'value': '7lô', 'key': '7lo'},{'id':[4,5], 'value': 'Blô', 'key': 'blo'}],
						"3d": [{'id':[6], 'value': 'Đầu', 'key': 'dau'},{'id':[7], 'value': 'Đuôi', 'key': 'duoi'},{'id':[8], 'value': '7lô', 'key': '7lo'},{'id':[9,10], 'value': 'Blô', 'key': 'blo'}],
						"4d": [{'id':[11], 'value': 'Đuôi', 'key': 'duoi'},{'id':[12,13], 'value': 'Blô', 'key': 'blo'}],
						"da": [{'id':[14,15,16], 'value': 'Đá thẳng', 'key': 'da'}],
						"dx": [{'id':[17,18,19], 'value': 'Đá xiên', 'key': 'dx'}]
					},
					4: {
						"2d": [{'id':[1], 'value': 'Đầu', 'key': 'dau'},{'id':[2], 'value': 'Đuôi', 'key': 'duoi'},{'id':[3], 'value': '7lô', 'key': '7lo'},{'id':[4,5], 'value': 'Blô', 'key': 'blo'}],
						"3d": [{'id':[6], 'value': 'Đầu', 'key': 'dau'},{'id':[7], 'value': 'Đuôi', 'key': 'duoi'},{'id':[8], 'value': '7lô', 'key': '7lo'},{'id':[9,10], 'value': 'Blô', 'key': 'blo'}],
						"4d": [{'id':[11], 'value': 'Đuôi', 'key': 'duoi'},{'id':[12,13], 'value': 'Blô', 'key': 'blo'}],
						"da": [{'id':[14,15,16], 'value': 'Đá thẳng', 'key': 'da'}]
					}
	        	},
	        	todayChannels: [],
	        	channel: [],
	        	data: [],
				filterVisible: { '2d': true, '3d': true, '4d': true, 'da': true, 'dx': true },
				propFilter: { '2d': 500, '3d': 500, '4d': 200, 'da': 100, 'dx': 100 }
	        }
	    },

	    components: {
        	'betted-table': BettedRatioTable
        },

        mounted(){
        	this.todayChannels = this.getchannelsToday();
        	this.channel = this.todayChannels[this.gameFilter];
        },

        watch: {
        	gameFilter: function(val){
        		let vm = this;
        		this.channel = this.todayChannels[val];
        		this.data = [];
        	}
        },

    	computed: {
			...mapGetters({
				user: 'user',
		  		gameInfo: 'gameInfo'
			})
		},

	    methods: {
	        getBettedRatioData: function(){
	        	let vm = this;
	        	let game = vm.gameFilter;
				
				let propFilter = Object.keys(vm.propFilter).map(e => { return e+"="+vm.propFilter[e]; });
				propFilter.push("game="+game);
				let params = propFilter.join("&");

	        	window.axios.get("admin-panel/tools/betted-ratio?"+params)
    				.then((res) => {
    					res = res.data;
    					vm.data = res.data;
    				})
    				.catch((error) => {

    				});
	        },
	        getchannelsToday: function() {
	    		let vm = this;
				let ccc = [];
				for(let game in this.gameInfo.channelToday){
					let data = [];

					if(game == 3) {
						data = vm.gameInfo.channelToday[game].map((el) => {
	        				let chns = el.channel_id.split(".").map((cns) => { return vm.gameInfo.channels[parseInt(cns)].alias; });
	        				return { id: el.channel_id, text: chns.join(", "), gameId: game };
	        			});
					}else{
						data = vm.gameInfo.channelToday[game].map((el) => {
	        				return { id: el.channel_id, text: vm.gameInfo.channels[parseInt(el.channel_id)].alias, gameId: game };
	        			});
					}

        			ccc = ccc.concat(data);
				}

				ccc = groupBy(ccc, function(val){
	    			return val.gameId;
	    		});

				return ccc;
			},
	    }
	}

</script>
