import { channelInfo, gameInfo, propInfo, channelTodayInfo, allXienChannelInfo } from './api';

const fetchChannelInfo = (context) => {
	return new Promise((resolve, reject) => {
		let fromStorage = JSON.parse(localStorage.getItem("channel"));
		if(fromStorage && Object.keys(fromStorage).length != 0){
			context.commit('GAME_INFO', {"channels": fromStorage});
	  		resolve(fromStorage);
		}else{
			channelInfo()
		    	.then((response) => {
		    		let data = {};
		    		$.each(response.data.data, (idx, e) => { data[e.id] = e; });
		    		localStorage.setItem("channel", JSON.stringify(data));
			  		context.commit('GAME_INFO', {"channels": data});
			  		resolve(data);
				})
				.catch((error) => {
			  		console.error(error);
			  		reject(error);
				});
		}
    });
};

