const fetchChannelTodayInfo = (context) => {
	return new Promise((resolve, reject) => {
		channelTodayInfo()
	    	.then((response) => {
	    		let data = {};
	    		$.each(response.data.data, (idx, e) => { 
	    			if(!data[e.game_id]) data[e.game_id] = [];
	    			data[e.game_id].push(e);
	    		});

		  		context.commit('GAME_INFO', {"channelToday": data});
		  		resolve(data);
			})
			.catch((error) => {
		  		console.error(error);
		  		reject(error);
			});
    });
};

const fetchXienChannelInfo = (context) => {
	return new Promise((resolve, reject) => {
		let fromStorage = JSON.parse(localStorage.getItem("xienChannels"));
		if(fromStorage && Object.keys(fromStorage).length != 0){
			context.commit('GAME_INFO', {"xienChannels": fromStorage});
	  		resolve(fromStorage);
		}else{
			allXienChannelInfo()
		    	.then((response) => {
		    		localStorage.setItem("xienChannels", JSON.stringify(response.data.data));
			  		context.commit('GAME_INFO', {"xienChannels": response.data.data});
			  		resolve(response.data.data);
				})
				.catch((error) => {
			  		console.error(error);
			  		reject(error);
				});
		}
    });
}

export default {
	fetchChannelInfo,
	fetchGameInfo,
	fetchPropInfo,
	fetchChannelTodayInfo,
	fetchXienChannelInfo
};